input[type="text"] {
    background-color: transparent;
    border: 0;
    border-bottom: solid 1px #B6167E;
    width: 30%;
    padding-bottom: 4px;
    color: #fff !important;
    font-weight: lighter;
    letter-spacing: 2px;
    margin-bottom: 30px;
    margin-right: 20px;
    font-size: 20px;
  }
  
  input[type="text"] { 
      outline: none;
  }
  
  input:-webkit-autofill {
      -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
      -webkit-text-fill-color: #fff !important;
  }
  
  button {
    border: 0;
    padding: 8px 20px;
    margin: 0 2px;
    border-radius: 2px;
    font-weight: lighter;
    letter-spacing: 1px;
    font-size: 15px;
    cursor: pointer;
    background-color: #B6167E;
    color: #fff;
    font-weight: 100;
  }
  
  button:active {
    outline: none;
  }